<template>
    <div style="padding: 0.75rem" class="container">
        <b-field v-if="items._id" label="Page ID">
            <b-input v-model="items._id" disabled></b-input>
        </b-field>
        <b-field label="Add Page Title">
            <b-input maxlength="100" placeholder="Specify a page title" v-model="items.title" @input="update"></b-input>
        </b-field>
        <b-field label="Add Description">
            <b-input maxlength="173" type="textarea" placeholder="Provide a brief description of this page for search engines to display on their results page" v-model="items.description" @input="update"></b-input>
        </b-field>
        <b-field label="Add Path">
            <b-input maxlength="100" placeholder="Specify a path e.g. /about-us" v-model="items.name" @input="update"></b-input>
        </b-field>
        <b-field label="Add Blocks">
            <b-taginput
            v-model="items.data"
            :data="filteredTags"
            autocomplete
            field="name"
            icon="label"
            placeholder="Search for blocks to add to this path"
            @typing="getAsyncBlockData">
            </b-taginput>
        </b-field>
        <b-field v-if="items._id">
            <button class="button is-success is-fullwidth" @click.prevent="updatePath">Update Page</button>
        </b-field>
        <b-field v-else>
            <button class="button is-success is-fullwidth" @click.prevent="createPath">Create Page</button>
        </b-field>
        <b-field label="Search for an existing page">
            <b-autocomplete
                class="is rounded"
                v-model="results"
                :data="pages"
                placeholder="e.g. about-us"
                field="name"
                :loading="isFetching"
                @input="getAsyncPageData"
                @select="populate">
            </b-autocomplete>
        </b-field>
        <s-code :opts="code" :type="'s-page'"></s-code>
        <input class="button is-danger" type="reset" @click="reset">
        <br>
        <br>
        <p class="control" v-if="items._id">
            <button class="button is-danger" type="delete" @click="isComponentModalActive = true">Delete Page</button>
            <span class="help">Please copy the page id above before clicking the delete button.</span>
        </p>    
        <b-modal :width="300" :active.sync="isComponentModalActive">
            <section class="modal-card-body">
                <p class="subtitle">Are you sure you want to delete this page?</p>
                <b-field label="Please enter the page ID">
                    <b-input v-model="pageID"></b-input>
                </b-field>
                <p class="control" v-if="items._id">
                    <button class="button is-danger" type="delete" :disabled="!(pageID == items._id)" @click="deletePage">Delete Page</button>
                </p>
            </section>
        </b-modal>
    </div>
</template>

<script>
    import PathsService from '../PathsService'
    import debounce from 'lodash/debounce'
    export default {
        props: ['opts'],
        data() {
            return {
                pageID: '',
                isComponentModalActive: false,
                results: '',
                pages: [],
                code: {},
                items: {},
                filteredTags: [],
                isSelectOnly: false,
                types: null,
                data: [],
                isFetching: false,
                paths: [],
                error: ''
            }
        },
        watch: {
            items() {
                this.code = Object.assign({}, this.items);
            }
        },
        methods: {
            async deletePage() {
                if (this.pageID == this.items._id) {
                    await PathsService.deletePath(this.items._id);
                    this.reset();
                    this.isComponentModalActive = false;
                }
            },
            populate() {
                this.items = this.pages[0];
                this.results = '';
            },
            async getPaths() {
                try {
                    this.paths = await PathsService.getPaths();
                } catch(err) {
                    this.error = err.message;
                }

            },
            async createPath() {
                await PathsService.insertPath(this.code);
            },
            async updatePath() {
                await PathsService.updatePath(this.items._id, this.code);
            },
            reset() {
                if (this.opts != null) {
                    this.items = Object.assign({}, this.opts);
                }
            },
            update: debounce(function () {
                this.items = Object.assign({}, this.items);
            }, 200),
            store () {
                this.$http.post(
                    '/api/path', 
                    this.code,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    }
                ).then(response => {
                    this.$toast.open(response.data.response)
                }).catch((error) => {
                    throw error
                });
            },
            getFilteredTags(text) {
                this.filteredTags = this.data.filter((option) => {
                    return option.user.first_name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getAsyncBlockData: debounce(function (text) {
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/blocks?query=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.results.forEach((item) => this.filteredTags.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500),
            getAsyncPageData: debounce(function (text) {
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/paths?query=${text}`)
                    .then(({ data }) => {
                        data.forEach((item) => this.pages.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
