<template>
    <div style="padding: 0.75rem" class="container">
        <b-field label="Search Blocks">
            <b-taginput
            v-model="items.data"
            :data="filteredTags"
            autocomplete
            field="name"
            icon="label"
            placeholder="Search for blocks to add to this path"
            @typing="getAsyncBlockData">
            </b-taginput>
        </b-field>
        <b-table 
            :paginated="isPaginated"
            :per-page="perPage"
            detailed
            detail-key="_id"
            :data="items.data" 
            :columns="columns">
            <template slot="detail" slot-scope="props">
                <article class="media">
                    <div class="media-content">
                        <div class="content">
                            <p>
                                {{ JSON.stringify(props.row.data) }} 
                            </p>
                        </div>
                    </div>
                </article>
            </template>
        </b-table>
        <button class="button is-dark" @click="fetchAllBlocks">
            <span>Fetch all blocks</span>
        </button>
    </div>
</template>

<script>
    import BlocksService from '../BlocksService'
    import debounce from 'lodash/debounce'
    export default {
        props: ['opts'],
        data() {
            return {
                isPaginated: true,
                perPage: 5,
                filteredTags: [],
                blocks: [],
                items: {},
                columns: [
                    {
                        field: '_id',
                        label: 'ID',
                        width: '40'
                    },
                    {
                        field: 'name',
                        label: 'Name',
                        width: '40'
                    }
                 ]
            }
        },
        watch: {
            items() {
                this.code = Object.assign({}, this.items);
            }
        },
        methods: {
            async fetchAllBlocks() {
                try {
                    let blocks = await BlocksService.getBlocks();
                    this.$set(this.items, 'data', blocks)
                } catch(err) {
                    this.error = err.message;
                }
            },
            getAsyncBlockData: debounce(function (text) {
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/blocks?query=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.blocks.forEach((item) => this.filteredTags.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        }
    }
</script>
