<template>
    <aside class="menu">
        <s-menuitem :item="item" :display="display" v-for="(item, key) in items.blocks" :key="key" :index="key" @setDisplay="setDisplay"></s-menuitem>
    </aside>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: [],
                display: []
            }
        },
        watch: {
            opts() {
                if (typeof this.items.blocks == 'undefined' && this.settings.menu != null) {
                    this.$set(this, 'items', this.settings.menu);
                } else {
                    this.$set(this, 'items', this.opts);
                }
            }
        },
        methods: {
            setDisplay(item) {
                if (this.display.includes(item.name)) {
                    for (var i = 0; i < this.display.length; i++) { 
                        if (this.display[i] === item.name) {
                            this.display.splice(i, 1); 
                        }
                    }
                } else {
                    this.display = item.display.slice(0);
                }
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            } 
            if (typeof this.items.blocks == 'undefined' && this.settings.menu != null) {
                this.items = this.settings.menu;
            }
            if (this.items.display) {
                this.display = this.items.display;
            }
        }
    }
</script>
