<template>
    <div>
        <p v-if="item.category" class="menu-label">
            {{ item.category }}
        </p>
        <li :href="item.link" slot="trigger" :class="{'navbar-link': item.blocks}" @click="setDisplay(item)">
            <a :href="item.link" @click="setActive(item)">{{ item.title }}</a>
        </li>
        <b-collapse v-if="item.blocks" :open="open">
            <ul class="menu-list">
                <s-menuitem :item="item" v-for="(item, key) in item.blocks" :key="key" :index="key" :settings="settings"></s-menuitem>
            </ul>
        </b-collapse>
    </div>
</template>

<script>
    export default {
        props: ['item', 'settings', 'display'],
        data() {
            return {
                open: false,
                page: 'contact'
            }
        },
        computed: {
            navClasses() {
                return [
                    {'hidden': this.item.hidden ? this.item.hidden : false},
                    {'has-dropdown': this.item.dropdown},
                    {'has-dropdown-up': this.item.dropdownUp}
                ];
            }
        },
        watch: {
            display() {
                this.open = this.display.includes(this.item.name)
            }
        },
        methods: {
            setDisplay(item) {
                this.$emit('setDisplay', item);
            }
        },
        mounted: function () {
            if (typeof this.display != 'undefined') {
                this.open = this.display.includes(this.item.name)
            }
        }
    }
</script>

